<template>
  <div class="white">
    <my-toolbar/>

    <v-container grid-list-xl>
      <v-layout>

        <v-flex md4>
          <v-text-field
            append-icon="mic"
            class="my-0"
            flat
            label="Search"
            prepend-inner-icon="search"
            v-model="keyword"
          ></v-text-field>
          <v-card flat>
            <v-list 
              two-line >
              <v-subheader>
                List Lahan
              </v-subheader>

              <v-list-tile
                avatar
                v-for="(item, index) in filteredItems"
                :key="index"
                @click="changeActiveLahan(index)"
                v-bind:light="index == activeLahanIndex"
                v-bind:class="{ 'blue': index == activeLahanIndex }"
              >
                <v-list-tile-avatar>
                  <img :src="item.avatar">
                </v-list-tile-avatar>

                <v-list-tile-content>
                  <v-list-tile-title v-html="item.lokasi"></v-list-tile-title>
                </v-list-tile-content>

                <v-list-tile-action>
                  <v-icon>pencil</v-icon>
                </v-list-tile-action>
              </v-list-tile>
            </v-list>
          </v-card>
          
        </v-flex>

        <v-flex md8>
          
          <v-toolbar id="tab-toolbar" class="white" flat>
            <v-toolbar-title>
              <div v-if="activeLahanIndex != null" class="title">{{ activeLahan.lokasi }}</div>
              <div v-else>Silahkan Pilih lokasi</div>
            </v-toolbar-title>
          </v-toolbar>
          <v-tabs
            light
            grow
          >
            <!-- <div 
              style="flex-grow: 1; line-height: normal; display: flex; align-items: center; padding-left: 12px;"
            >
              <div v-if="activeLahanIndex != null" class="title">{{ activeLahan.lokasi }}</div>
              <div v-else>Silahkan Pilih lokasi</div>
            </div> -->
            <v-tabs-slider></v-tabs-slider>

            <v-tab href="#mymap">
              Map
            </v-tab>

            <v-tab href="#tab-2">
              Info
            </v-tab>

            <v-tab href="#tab-3">
              Galeri
            </v-tab>

            <v-tab-item id="mymap">

            </v-tab-item>
            <v-tab-item id="tab-2">
              <v-container>
                <v-text-field 
                  label="Lokasi"
                  :value="activeLahan.lokasi"
                  readonly></v-text-field>

                <v-text-field 
                  label="Curah Hujan"
                  :value="activeLahan.curahHujan"
                  readonly></v-text-field>

                <v-text-field 
                  label="Sistem Pengairan"
                  :value="activeLahan.sistemPengairan"
                  readonly></v-text-field>

                <v-text-field 
                  label="Sumber Air"
                  :value="activeLahan.sumberAir"
                  readonly></v-text-field>

                <v-text-field 
                  label="Kemiringan Lahan"
                  :value="activeLahan.kemiringanLahan"
                  readonly></v-text-field>

                <v-text-field 
                  label="Tekstur Tanah"
                  :value="activeLahan.teksturTanah"
                  readonly></v-text-field>

                <v-text-field 
                  label="C-Organik"
                  :value="activeLahan.corganik"
                  readonly></v-text-field>

                <v-text-field 
                  label="Kedalaman Tanah"
                  :value="activeLahan.kedalamanTanah"
                  readonly></v-text-field>
                  
              </v-container>
            </v-tab-item>
          </v-tabs>
        </v-flex>

        <!-- <v-flex md3>
          <v-list>
            <v-list-tile>
              <v-list-tile-action>
                <v-icon>
                  directions_run
                </v-icon>
              </v-list-tile-action>
              <v-list-tile-title>
                Map
              </v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-flex> -->
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import MyToolbar from './MyToolbar'
import { getData } from '../services/lahan'

const defLahan = {
  lokasi: 'oesapa',
  avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
  lat: -10.179173,
  long: 123.591715,
  curahHujan: 448,
  sistemPengairan: 'Irigasi Permukaan',
  sumberAir: 'Banyak',
  kemiringanLahan: 'Datar',
  teksturTanah: 'Halus',
  corganik: 'Tersedia',
  bahayaBencana: 'Ringan',
  kedalamanTanah: 48
}

export default {
  components: {
    MyToolbar
  },
  data () {
    return {
      items: [],
      sidebarWidth: 400,
      activeLahanIndex: null,
      keyword: ''
    }
  },

  created () {
    getData()
      .then(items => {
        this.items = items
      })
      .catch(err => {
        console.log(err)
      })
  },

  computed: {
    activeLahan () {
      if (this.activeLahanIndex == null) {
        return defLahan
      } else {
        return this.items[this.activeLahanIndex]
      }
    },
    filteredItems () {
      let upperIndex = this.keyword.toUpperCase()
      return this.items.filter(it => {
        let loc = it.lokasi
        return loc.toUpperCase().indexOf(upperIndex) !== -1
      })
    }
  },

  methods: {
    sayHi () {
      console.log('Hi')
    },
    createMap () {
      let defLoc = [51.505, -0.09]
      if (this.activeLahanIndex != null) {
        // Put location here. activeLahan is defined.
        defLoc[0] = this.activeLahan.long
        defLoc[1] = this.activeLahan.lat
      }
      // eslint-disable-next-line
      var mymap = L.map('mymap').setView(defLoc, 13)
      // eslint-disable-next-line
      L.tileLayer('https://api.tiles.mapbox.com/v4/{id}/{z}/{x}/{y}.png?access_token={accessToken}', {
        attribution: 'Map data &copy; <a href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors, <a href="https://creativecommons.org/licenses/by-sa/2.0/">CC-BY-SA</a>, Imagery © <a href="https://www.mapbox.com/">Mapbox</a>',
        maxZoom: 18,
        id: 'mapbox.streets',
        accessToken: 'pk.eyJ1Ijoiam9tZXRhIiwiYSI6ImNqbTB4eWtqNzB5dzAzcHJ2NGVoaW5wcXAifQ.yXaPLSgJXcn0shC2nYDU0g'
      }).addTo(mymap)
    },
    changeActiveLahan (index) {
      this.activeLahanIndex = index
      setTimeout(() => {
        this.createMap()
      }, 500)
    }
  },

  mounted () {
    // eslint-disable-next-line
    this.createMap()
  }
}
</script>

<style>
/* .v-list__tile.v-list__tile--avatar.theme--light {
  border-bottom: 1px solid #DDD;
} */
#mymap {
  height: 500px;
}

/* Imitate button look, and make it match the toolbar height */
#add-area {
  height: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#tab-toolbar .v-toolbar__content {
  justify-content: center;
}
</style>



